import type { IObject } from '../../interfaces/IObject'
import { BufferGeometry, EllipseCurve, Line, LineBasicMaterial, Material, Texture } from 'three'
import { getSemiMinorAxis } from '../../helpers/formulas'
import { degToRad } from '../../helpers/converter'
import { IShape, ThreeObject } from '../../interfaces/IShape'
import { getDistance } from '../../helpers/scaling'

export class Ellipse implements IShape {
  object: IObject

  constructor (object: IObject) {
    this.object = object
  }

  makeTexture (): Texture | null {
    return null
  }

  makeGeometry (): BufferGeometry {
    const semiMajorAxis = getDistance(this.object.semiMajorAxis)
    const semiMinorAxis = getSemiMinorAxis(semiMajorAxis, this.object.orbitalEccentricity)
    const curve = new EllipseCurve(0, 0, semiMajorAxis, semiMinorAxis, 0, 2 * Math.PI, false, 0)
    const points = curve.getPoints(150)
    return new BufferGeometry().setFromPoints(points)
  }

  makeMaterial (): Material {
    return new LineBasicMaterial({ color: this.object.color })
  }

  makeShape (): ThreeObject {
    return new Line(this.makeGeometry(), this.makeMaterial())
  }

  prepareShape (): ThreeObject {
    const shape = this.makeShape()
    shape.name = this.object.name + 'Orbit'
    shape.rotation.x = degToRad(this.object.orbitalInclination)
    shape.rotation.z = degToRad(this.object.lpe) + degToRad(this.object.lan)

    return shape
  }
}
