export class Sphere implements IShape {
  public object: IObject
  private readonly _strategy: ObjectStrategy
  private readonly scene: Scene

  constructor (object: IObject, strategy: ObjectStrategy, scene: Scene) {
    this.object = object
    this._strategy = strategy
    this.scene = scene
  }

  makeTexture (): Texture | null {
    if (this.object.texture) {
      return new TextureLoader().load(getTexturePath(this.object.texture))
    } else {
      return null
    }
  }

  makeGeometry (): BufferGeometry {
    return new SphereGeometry(0.5, 64, 64)
  }

  makeMaterial (): Material {
    return this._strategy.getMaterial(this.makeTexture())
  }

  makeShape (): ThreeObject {
    return new Mesh(this.makeGeometry(), this.makeMaterial())
  }

  makeLabel (): CSS2DObject {
    const div = document.createElement('div')
    div.className = 'label-name'
    div.textContent = this.object.name
    div.style.backgroundColor = 'transparent'

    const label = new CSS2DObject(div)
    label.position.set(this.object.x, this.object.y, this.object.z)

    return label
  }

  prepareShape (): ThreeObject {
    const rotationSpeed = this._strategy.calculateRotationSpeed(this.object)
    const shape = this.makeShape()

    shape.name = this.object.name
    shape.castShadow = this._strategy.castShadow()
    shape.receiveShadow = this._strategy.receiveShadow()
    shape.userData.staticData = this.object
    shape.userData.computedData = { rotationSpeed }
    shape.rotation.x = degToRad(this.object.axialTilt + 90)

    this._strategy.setCoordinates(<Mesh>shape, this.object.x, this.object.y, this.object.z)

    shape.add(this.makeLabel())

    if (this.object.hasRings) {
      shape.add(this._strategy.addRings())
    }

    if (this._strategy.addOrbit() !== null) {
      this.scene.add(this._strategy.addOrbit())
    }

    return shape
  }
}
