
import { defineComponent } from 'vue'
export default defineComponent({
  name: 'SpeedSwitcher',
  data () {
    return {
      allowedUnitTypes: ['au/s', 'km/s'],
      unitType: 'au/s',
      speed: 0,
      buttons: [
        {
          iconCode: 'do_not_disturb_on',
          text: 'Stop',
          value: 0,
          operation: 'set'
        },
        {
          iconCode: 'first_page',
          text: 'Minimum speed',
          value: 0.0001,
          operation: 'set'
        },
        {
          iconCode: 'keyboard_double_arrow_left',
          text: 'Decrease speed by 10 units',
          value: 10,
          operation: 'sub'
        },
        {
          iconCode: 'chevron_left',
          text: 'Decrease speed by 1 unit',
          value: 1,
          operation: 'sub'
        },
        {
          iconCode: 'chevron_right',
          text: 'Increase speed by 1 unit',
          value: 1,
          operation: 'add'
        },
        {
          iconCode: 'keyboard_double_arrow_right',
          text: 'Increase speed by 10 units',
          value: 10,
          operation: 'add'
        },
        {
          iconCode: 'last_page',
          text: 'Maximum speed',
          value: -1,
          operation: 'set'
        },
        {
          iconCode: 'expand_content',
          text: 'Switch speed units (au/s or km/s)',
          value: 0,
          operation: 'default'
        }
      ]
    }
  },
  methods: {
    setSpeed (speed: number, operation?: string) {
      if (speed === -1) speed = this.defineMaximumSpeed()
      switch (operation) {
        case 'add':
          if (this.speed + speed > this.defineMaximumSpeed()) {
            this.speed = this.defineMaximumSpeed()
            return
          }
          this.speed += speed
          break
        case 'sub':
          if (this.speed - speed < 0) {
            this.speed = 0
            return
          }
          this.speed -= speed
          break
        case 'set':
          this.speed = speed
          break
        default:
          this.speed = 0
          this.setUnitType(this.unitType)
          break
      }

      this.$emit('change-speed', this.speed)
    },
    setUnitType (unitType: string) {
      if (!this.allowedUnitTypes.includes(unitType)) return
      if (unitType === 'au/s') this.unitType = 'km/s'
      else this.unitType = 'au/s'

      this.$emit('change-unit-type', this.unitType)
    },
    defineMaximumSpeed () {
      if (this.unitType === 'au/s') return 25
      else return 300000
    }
  }
})
