import type { IObject } from '../interfaces/IObject'
import { Config } from '../classes/Config'

export function getSemiMinorAxis (a: number, e: number): number {
  return a * (1 - Math.pow(e, 2))
}

export function getPeriapsis (a: number, e: number): number {
  return a * (1 - e)
}

export function getApoapsis (a: number, e: number): number {
  return a * (1 + e)
}

export function getFocus (a: number, b: number): number {
  return Math.sqrt(Math.pow(a, 2) - Math.pow(b, 2))
}

export function getVelocityVector (parent: IObject, satellite: IObject) {
  const u = Config.G * (parent.m + satellite.m)
  return u * ((2 / satellite.distanceFromParent) - (1 / satellite.semiMajorAxis)) / 365.25
}
