import type { IObject } from '../../interfaces/IObject'
import { IRing } from '../../interfaces/IRing'
import {
  BufferGeometry,
  DoubleSide,
  Material,
  Mesh,
  MeshStandardMaterial,
  RingGeometry,
  Texture,
  TextureLoader,
  Vector3
} from 'three'
import { degToRad } from '../../helpers/converter'
import planetaryRings from '../../../data/SolarSystem/planetaryRings'
import { IShape, ThreeObject } from '../../interfaces/IShape'
import { getTexturePath } from '@/core/helpers'

export class Ring implements IShape {
  public object: IObject
  public ringData: IRing

  constructor (object: IObject) {
    this.object = object
    this.ringData = planetaryRings.find(object => object.objectName === this.object.name)!
  }

  makeTexture (): Texture | null {
    return new TextureLoader().load(getTexturePath(this.ringData.map))
  }

  makeGeometry (): BufferGeometry {
    const geometry = new RingGeometry(this.ringData.innerRadius, this.ringData.outerRadius, 128)

    const pos = geometry.getAttribute('position')
    const v3 = new Vector3()

    for (let i = 0; i < pos.count; i++) {
      v3.fromBufferAttribute(pos, i)
      geometry.attributes.uv.setXY(i, v3.length() < 4 ? 0 : 1, 1)
    }

    return geometry
  }

  makeMaterial (): Material {
    return new MeshStandardMaterial({
      map: this.makeTexture(),
      alphaMap: this.makeTexture(),
      side: DoubleSide,
      transparent: true,
      alphaTest: this.ringData.alphaTest
    })
  }

  makeShape (): ThreeObject {
    return new Mesh(this.makeGeometry(), this.makeMaterial())
  }

  prepareShape (): ThreeObject {
    const shape = this.makeShape()
    shape.castShadow = true
    shape.receiveShadow = true
    shape.rotation.x = degToRad(90)

    return shape
  }
}
