import { DoubleSide, Mesh, MeshBasicMaterial, SphereGeometry } from 'three'
import { getDistance } from '../helpers/scaling'
import { Environment } from './components/Environment'

class Universe {
  draw () {
    const geometry = new SphereGeometry(getDistance(1000), 64, 64)
    const material = new MeshBasicMaterial({ color: '#000', opacity: 0, side: DoubleSide })
    const mesh = new Mesh(geometry, material)

    mesh.name = 'Universe'

    const stars = new Environment()

    mesh.add(stars.draw())

    return mesh
  }
}

export { Universe }
