import {
  AmbientLight,
  AxesHelper,
  Camera,
  CameraHelper,
  DirectionalLight,
  PerspectiveCamera,
  PointLight,
  Scene,
  WebGL1Renderer,
  WebGLRenderer
} from 'three'
import { Config } from './Config'
import { getObjectStrategy } from '../helpers'
import { PCFSoftShadowMap } from 'three/src/constants'
import { getDistance } from '../helpers/scaling'
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass'
import { Sphere } from '../StarForge/objects/Sphere'
import { ShapeDrawer } from './ShapeDrawer'
import { FlyControls } from 'three/examples/jsm/controls/FlyControls'
import { CSS2DRenderer } from 'three/examples/jsm/renderers/CSS2DRenderer'
import { Universe } from '../StarForge/Universe'

export class App {
  static width = window.innerWidth
  static height = window.innerHeight

  public static init (canvas: HTMLCanvasElement) {
    const labelRenderer = this.createLabelRenderer()
    const renderer = this.createRenderer(canvas)
    const composer = this.createComposer(renderer)
    const scene = this.createScene()
    const camera = this.createCamera()
    const light = this.createLight()
    const pointLight = this.createPointLight()
    const directionalLight = this.createDirectionalLight()
    const controls = this.createControls(camera, renderer)
    const axes = this.createAxesHelper()
    const helper = new CameraHelper(pointLight.shadow.camera)
    scene.add(helper)

    const renderPass = new RenderPass(scene, camera)

    camera.position.set(0, 0, getDistance(5))

    scene.add(camera)
    // scene.add(light)
    // scene.add(axes)

    this.createObjects(scene)

    const sun = scene.getObjectByName('Sun')!

    scene.add(pointLight)
    console.log(pointLight)
    pointLight.position.set(sun.position.x, sun.position.y, sun.position.z)

    composer.addPass(renderPass)

    return { labelRenderer, composer, scene, camera, controls }
  }

  private static createLabelRenderer () {
    const renderer = new CSS2DRenderer()
    renderer.setSize(App.width, App.height)
    renderer.domElement.style.position = 'absolute'
    renderer.domElement.style.top = '0px'

    return renderer
  }

  private static createRenderer (canvas: HTMLCanvasElement) {
    canvas.setAttribute('width', `${App.width}`)
    canvas.setAttribute('height', `${App.height}`)

    const renderer = new WebGL1Renderer({ canvas, antialias: true, logarithmicDepthBuffer: true, alpha: true })
    renderer.shadowMap.enabled = true
    renderer.shadowMap.type = PCFSoftShadowMap

    return renderer
  }

  private static createComposer (renderer: WebGLRenderer) {
    return new EffectComposer(renderer)
  }

  private static createScene () {
    return new Scene()
  }

  private static createCamera () {
    return new PerspectiveCamera(45, App.width / App.height, 0.0005, getDistance(900))
  }

  private static createLight () {
    return new AmbientLight(0xffffff, 1)
  }

  private static createPointLight () {
    const light = new PointLight(0xffffff, 0.5, getDistance(200), 2)
    light.castShadow = true
    // light.shadow.normalBias = 1
    light.shadow.bias = -0.0001
    light.shadow.mapSize.width = 2048 * 4
    light.shadow.mapSize.height = 2048 * 4
    light.shadow.camera.near = 1
    light.shadow.camera.far = getDistance(200)
    light.name = 'PointLight'
    light.shadow.camera.visible = true
    return light
  }

  private static createDirectionalLight () {
    return new DirectionalLight(0xffffff, 1)
  }

  private static createControls (camera: Camera, renderer: WebGLRenderer) {
    const controls = new FlyControls(camera, renderer.domElement)
    // controls.movementSpeed = 0.01
    controls.rollSpeed = 0.001
    controls.autoForward = false
    controls.dragToLook = true

    return controls
  }

  private static createAxesHelper () {
    return new AxesHelper(5000)
  }

  private static createObjects (scene: Scene) {
    const universe = new Universe()
    const universeSphere = universe.draw()
    scene.add(universeSphere)

    for (const object of Config.SimulationScenario) {
      const drawer = new ShapeDrawer(object)
      const shape = drawer.draw(new Sphere(object, getObjectStrategy(object), scene))
      scene.add(shape)
    }

    /* if (asteroidBelts.length) {
      for (const asteroidBelt of asteroidBelts) {
        const shape = new AsteroidBelt(asteroidBelt.radius)
        scene.add(shape.draw())
      }
    } */
  }
}
