import type { IObject } from '../../interfaces/IObject'
import type { Mesh, Material } from 'three'
import { ObjectStrategy } from './ObjectStrategy'
import { MeshBasicMaterial } from 'three'

export class SunStrategy implements ObjectStrategy {
  public object: IObject

  constructor (object: IObject) {
    this.object = object
  }

  getMaterial (texture?: any): Material {
    if (texture) {
      return new MeshBasicMaterial({ map: texture })
    } else {
      return new MeshBasicMaterial({ color: this.object.color })
    }
  }

  calculateRotationSpeed (object: IObject): number {
    return ((2 * Math.PI) / (365.2 * this.object.rotationPeriod * 60)) / 25.38
  }

  setCoordinates (object: Mesh, x: number, y: number, z: number): void {
    object.position.set(x, y, z)
  }

  addOrbit (): any {
    return null
  }

  addRings (): any {
    //
  }

  castShadow (): boolean {
    return false
  }

  receiveShadow (): boolean {
    return false
  }
}
