import type { IShape, ThreeObject } from '../interfaces/IShape'
import type { IObject } from '../interfaces/IObject'

class ShapeDrawer {
  object: IObject

  constructor (object: IObject) {
    this.object = object
  }

  draw (shape: IShape): ThreeObject {
    const object = shape.prepareShape()
    object.scale.multiplyScalar((this.object.diameter * 1000) / 1000000000)

    return object
  }
}

export { ShapeDrawer }
