import { Config } from '../classes/Config'

export function visibleDiameter (diameter: number): number {
  return Config.AU / (diameter * 1000)
}

export function visibleRadius (diameter: number): number {
  return (Config.AU / (diameter * 1000)) / 2
}

/**
 * Get the distance between objects in astronomical units
 * @param distance format type: astronomical unit
 */
export function getDistance (distance: number): number {
  return (distance * Config.AU) / 1000000000
}

/**
 * Get the object mass
 * @param mass format type: solar mass
 */
export function getObjectMass (mass: number): number {
  return (mass * Config.SolarMass) * Config.ObjectScale
}
