import type { IObject } from '../interfaces/IObject'

export class N {
  G: number
  dt: number
  S: number

  constructor (G: number, dt: number, S: number) {
    this.G = G
    this.dt = dt
    this.S = S
  }

  updateCoordinates (objects: IObject[]): void {
    for (let i = 0; i < objects.length; i++) {
      let ax = 0
      let ay = 0
      let az = 0
      const objectI = objects[i]
      for (let j = 0; j < objects.length; j++) {
        if (i !== j) {
          const objectJ = objects[j]
          const dx = objectJ.x - objectI.x
          const dy = objectJ.y - objectI.y
          const dz = objectJ.z - objectI.z
          const dSq = dx * dx + dy * dy + dz * dz
          const f = (this.G * objectJ.m) / (dSq * Math.sqrt(dSq + this.S))
          ax += dx * f
          ay += dy * f
          az += dz * f
        }
      }
      objectI.vx += ax * this.dt
      objectI.vy += ay * this.dt
      objectI.vz += az * this.dt

      objectI.x += objectI.vx * this.dt
      objectI.y += objectI.vy * this.dt
      objectI.z += objectI.vz * this.dt
    }
  }
}
