import { BufferAttribute, BufferGeometry, Points, PointsMaterial, TextureLoader, Vector3 } from 'three'
import { randFloat } from 'three/src/math/MathUtils'
import { getDistance } from '../../helpers/scaling'
import { getTexturePath } from '@/core/helpers'

class Environment {
  draw () {
    const v = new Vector3()

    function randomPointsInSphere (radius: number) {
      const x = randFloat(-1, 1)
      const y = randFloat(-1, 1)
      const z = randFloat(-1, 1)
      const normalizationFactor = 1 / Math.sqrt(x * x + y * y + z * z)

      v.x = x * normalizationFactor * radius
      v.y = y * normalizationFactor * radius
      v.z = z * normalizationFactor * radius

      return v
    }

    const count = 10000
    const geometry = new BufferGeometry()
    const positions = []

    for (let i = 0; i < count; i++) {
      const vertex = randomPointsInSphere(getDistance(999))
      positions.push(vertex.x, vertex.y, vertex.z)
    }

    geometry.setAttribute('position', new BufferAttribute(new Float32Array(positions), 3))

    const texture = new TextureLoader().load(getTexturePath('asteroid.jpg'))
    const material = new PointsMaterial({ map: texture, size: 0.5, sizeAttenuation: true })

    return new Points(geometry, material)
  }
}

export { Environment }
