import type { IObject } from '../../interfaces/IObject'
import type { Material } from 'three'
import { Mesh, MeshStandardMaterial } from 'three'
import { ObjectStrategy } from './ObjectStrategy'
import { Ellipse } from '../objects/Ellipse'
import { Ring } from '../objects/Ring'

export class PlanetStrategy implements ObjectStrategy {
  public object: IObject

  constructor (object: IObject) {
    this.object = object
  }

  getMaterial (texture?: any): Material {
    if (texture) {
      return new MeshStandardMaterial({ map: texture, opacity: 1 })
    } else {
      return new MeshStandardMaterial({ color: this.object.color, opacity: 1 })
    }
  }

  calculateRotationSpeed (object: IObject): number {
    return (2 * Math.PI) / (this.object.orbitalPeriod * this.object.rotationPeriod * 60)
  }

  setCoordinates (object: Mesh, x: number, y: number, z: number): void {
    object.position.set(x, y, z)
  }

  addOrbit (): any {
    const orbit = new Ellipse(this.object)
    return orbit.prepareShape()
  }

  addRings (): any {
    const ring = new Ring(this.object)
    const shape = ring.prepareShape()
    shape.scale.multiplyScalar(0.2)

    return shape
  }

  castShadow (): boolean {
    return true
  }

  receiveShadow (): boolean {
    return true
  }
}
