
import { defineComponent } from 'vue'
import { Config } from '@/core/classes/Config'
import { IObject } from '@/core/interfaces/IObject'

export default defineComponent({
  name: 'ObjectList',
  data () {
    return {
      objects: Config.SimulationScenario
    }
  },
  methods: {
    defineObjectIcon (object: IObject) {
      if (object.strategy === 'SunStrategy') return 'sunny'
      else return 'language'
    },
    selectObject (object: IObject) {
      this.$emit('click-object', object)
    }
  }
})
