import type { IObject } from '../interfaces/IObject'
import { SunStrategy } from '../StarForge/ObjectStrategies/SunStrategy'
import { PlanetStrategy } from '../StarForge/ObjectStrategies/PlanetStrategy'
import { Config } from '@/core/classes/Config'

export function getObjectStrategy (object: IObject): SunStrategy | PlanetStrategy {
  switch (object.strategy) {
    case 'SunStrategy':
      return new SunStrategy(object)
    case 'PlanetStrategy':
      return new PlanetStrategy(object)
    default:
      return new PlanetStrategy(object)
  }
}

export function generateTexture () {
  const canvas = document.createElement('canvas')
  canvas.width = 2
  canvas.height = 2

  const context = canvas.getContext('2d')!
  context.fillStyle = 'white'
  context.fillRect(0, 1, 2, 1)

  return canvas
}

export function groupBy (key: string) {
  return function group (array: any[]) {
    return array.reduce((acc, obj) => {
      const property = obj[key]
      acc[property] = acc[property] || []
      acc[property].push(obj)
      return acc
    }, {})
  }
}

export function getAllObjects (data: IObject[]) {
  let result: IObject[] = []
  data.forEach((a) => {
    result.push(a)
    if (Array.isArray(a.satellites)) {
      result = result.concat(getAllObjects(a.satellites))
    }
  })
  return result
}

export function getTexturePath (texture: string) {
  return require(`@/assets/textures/${texture}`)
}

export function calculateSpeed (speed: number, unitType: string): number {
  const sceneAU = Config.AU * Config.ScalingFactor * 0.01
  const sceneKM = 10 * Config.ScalingFactor
  let resultSpeed

  if (unitType === 'au/s') resultSpeed = sceneAU * speed
  else resultSpeed = sceneKM * speed

  return resultSpeed
}
