
import { defineComponent } from 'vue'
import { Tween, Easing, update as tweenUpdate } from '@tweenjs/tween.js'
import ObjectList from '@/components/ObjectList.vue'
import SpeedSwitcher from '@/components/SpeedSwitcher.vue'
import { Config } from '@/core/classes/Config'
import { App } from '@/core/classes/App'
import { N } from '@/core/classes/N'
import { Mesh, Vector3 } from 'three'
import { getDistance } from '@/core/helpers/scaling'
import { calculateSpeed } from '@/core/helpers'
import type { IObject } from '@/core/interfaces/IObject'

export default defineComponent({
  name: 'App',
  components: {
    ObjectList,
    SpeedSwitcher
  },
  data () {
    return {
      speed: 0,
      unitType: 'au/s'
    }
  },
  mounted () {
    const canvas: HTMLCanvasElement = document.querySelector('#canvas')!
    const { labelRenderer, composer, scene, camera, controls } = App.init(canvas)

    document.addEventListener('moveTo', (e) => {
      const object = (e as CustomEvent).detail
      const distance = object.diameter / 300000
      const objectPosition = new Vector3(getDistance(object.x), getDistance(object.y), getDistance(object.z))
      const destinationPosition = new Vector3(objectPosition.x - distance, objectPosition.y, objectPosition.z - distance)
        .sub(objectPosition)
        .setLength(distance)
        .add(objectPosition)

      const tween = new Tween(camera.position)

      tween.to(destinationPosition, 7500)
        .easing(Easing.Quadratic.InOut)
        .onUpdate(() => {
          camera.lookAt(objectPosition)
        })
        .onComplete(() => {
          camera.lookAt(objectPosition)
        })
      tween.start()
    })

    document.body.appendChild(labelRenderer.domElement)

    const objects: IObject[] = scene.children.filter(object => object instanceof Mesh && object.name !== 'Universe').map(object => object.userData.staticData)
    objects.forEach(object => {
      object.vx *= 365.25
      object.vy *= 365.25
      object.vz *= 365.25
    })

    const SolarSystem = new N(Config.G, Config.dt, Config.S)

    const animate = () => {
      // SolarSystem.updateCoordinates(objects)
      requestAnimationFrame(animate)

      controls.movementSpeed = calculateSpeed(this.speed, this.unitType)
      controls.update(1)
      tweenUpdate()

      for (let i = 0; i < objects.length; i++) {
        const object = objects[i]
        const x: number = object.x
        const y: number = object.y
        const z: number = object.z
        const model = scene.getObjectByName(object.name)!
        model.position.x = getDistance(x)
        model.position.y = getDistance(y)
        model.position.z = getDistance(z)
      }

      labelRenderer.render(scene, camera)
      composer.render()
    }

    console.log('  !', scene)

    animate()
  },
  methods: {
    changeSpeed (speed: number) {
      this.speed = speed
    },
    changeUnitType (unitType: string) {
      this.unitType = unitType
    },
    selectObject (object: IObject) {
      const move = new CustomEvent('moveTo', { detail: object })
      document.dispatchEvent(move)
    }
  }
})
